.form{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.form_list{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.form_item{
    display: flex;
    align-items: center;
}

.form_title{
    margin-right: 10px;
    justify-content: flex-start;
    color: white;
}

.form_input{
border-radius: 5px;
border: none;
}

.form_button{
    margin-top: 10px;
    border: solid 2px green;
    border-radius: 5px;
    color: rgb(250, 244, 244);
    background-color: green;
    font-size: medium;
    font-weight: 500;
    
}